@import url(https://fonts.googleapis.com/css?family=Quicksand:300,regular,500,600,700);
@import url(http://fonts.cdnfonts.com/css/monument-extended);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Quicksand';
  -webkit-font-smoothing: antialiased;
}

img {
  pointer-events: none;
}

html,
body {
  background: #0f0f0f;
}

body {
  background: #0f0f0f;
  min-height: 100vh;
  width: 100%;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-weight: 500;
    font-family: 'Quicksand';
    -webkit-font-smoothing: antialiased;
}

header.mainBody {
    position: fixed;
    width: 100%;
    height: 100%;
    background: #0f0f0f;
}

html,
body {
    position: relative;
    background: #0f0f0f;
    width: 100%;
    height: 100%;
}

.navbar {
    padding-top: 2rem;
    background: #111 !important;
}

.rightLinks {
    margin-right: 4rem;
}
.mainLogo {
    margin-left: 4rem;
    color: #f1f1f1;
    opacity: 0.9;
}
.navlink {
    margin: 0 10px;
}

.hero__seciton {
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    background: #0f0f0f;
    z-index: 2;
}

.mainText {
    margin-top: 4rem;
    font-family: 'Poppins';
    padding: 10px;
    font-size: 4.1rem;
    text-align: center;
}
.typeText {
    font-weight: 500;
    color: #777;
}

.introText {
    font-family: 'Poppins';
    font-weight: 300;
    text-align: center;
    font-size: 1.5rem;
    color: #ccc;
    opacity: 0.8;
    margin-top: 1.2rem;
}

.btnContainer {
    text-align: center;
    margin-top: 2.6rem;
}

.mainImg {
    position: fixed;
}

.btnContainer .btn {
    text-decoration: none;
    background: #0353ff;
    /* background: #1b1b1b; */
    color: #fff;
    padding: 12px 80px;
    max-width: 250px;
    width: 98%;
    font-weight: 400;
    outline: none;
    font-family: 'Poppins';
    /* center */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 10px;
}

.btn:hover {
    color: #000 !important;
    background: #fff;
}

.logoTextMain {
    font-family: 'Monument Extended';
    font-weight: lighter;
}

/* Image */
.img2img {
    position: fixed;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
.img3img {
    position: fixed;
    right: -50%;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
.img4img {
    position: fixed;
    -webkit-transform: scale(0.15);
            transform: scale(0.15);
}
.img5img {
    position: fixed;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    left: -78%;
    bottom: -50%;
}
.img10img {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    right: -35%;
    bottom: -60%;
}

img {
    pointer-events: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: 1;
}

